.common-container {
  min-width: fit-content;
  flex: 1;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-right: 8px;
  margin-bottom: 8px;
  overflow: auto;
  padding: 8px;
}
@media screen and (max-width: 746px) {
  .common-container {
    max-width: 360px;
  }
}
.counter-container {
  min-width: 360px;
}
.counter-container .counter-item {
  margin-right: 24px;
}
.counter-container .counter-text {
  display: inline-block;
  width: 24px;
  text-align: center;
}
.counter-container button {
  cursor: pointer;
}
.todo-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.todo-wrapper .todo-title {
  font-weight: bold;
  text-align: center;
  line-height: 16px;
}
.todo-wrapper .todo-title i {
  font-size: 16px;
  display: block;
  margin-bottom: 10px;
  opacity: 0.6;
  text-shadow: 2px 2px 2px #999;
}
.todo-wrapper .todo-create input {
  width: 100%;
  padding: 4px 8px;
  font-size: 16px;
}
.todo-wrapper ul {
  list-style: none;
  margin: 0;
  padding: 0;
  max-width: 300px;
}
.todo-wrapper ul .display {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 6px 0;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}
.todo-wrapper ul .display .item {
  flex: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.todo-wrapper ul .display .item span {
  font-size: 20px;
  margin-left: 4px;
}
.todo-wrapper ul .display .item .done {
  text-decoration: line-through;
  font-style: italic;
}
.todo-wrapper ul .display .remove {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}
.todo-wrapper ul .edit input {
  width: 100%;
  padding: 4px 8px;
  font-size: 16px;
}
.todo-wrapper .todo-state {
  height: 30px;
  line-height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.todo-wrapper .todo-state span {
  font-size: 14px;
}
.todo-wrapper .todo-state .number {
  font-weight: bold;
  margin-right: 3px;
}
.todo-wrapper .todo-state a {
  margin-left: 10px;
}
.contact-list {
  width: 360px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-right: 8px;
  margin-bottom: 8px;
}
.contact-list dt,
.contact-list dd {
  margin: 0;
}
.contact-list dt {
  padding: 10px;
  font-weight: bold;
  background-color: #eee;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
}
.contact-list dt:hover {
  background-color: #ddd;
}
.contact-list dd {
  display: none;
  padding: 10px;
  border-bottom: 1px solid #bbb;
}
.contact-list .expanded dd {
  display: block;
}
.contact-list .expanded .checked {
  background: green;
}
.context-container {
  width: 360px;
  min-width: fit-content;
  box-sizing: border-box;
  background-color: #eee;
  margin-right: 8px;
  margin-bottom: 8px;
  overflow: auto;
}
.context-container .context-item {
  padding: 0 8px;
}
